import Vue from 'vue/dist/vue.js'
import BootstrapVue from 'bootstrap-vue/dist/bootstrap-vue.js'

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

Vue.use(BootstrapVue)

// Vue Global Components
var vueGlobalComponents = {};

function importVueComponents (r) {
    r.keys().forEach(key => {
        vueGlobalComponents[key.replace(/(^\.\/)|(\.vue$)/ig, '')] = r(key).default;
    });
}

importVueComponents(require.context("./components/", false, /\.vue$/i));

for (var key in vueGlobalComponents) {
    if (vueGlobalComponents.hasOwnProperty(key)) {
      Vue.component(key, vueGlobalComponents[key]);
    }
}

